var $$ = null;

const axutils = 
{
    Init : function ( context ) { $$ = context },
    RegisterPageLoadEvent : function ( name, handler )
    {
        $$(document).on('page:init', '.page[data-name="' + name + '"]', function (e) 
        {
            handler ( e );
        } );
    },
    $$ : function(x) { return $$(x); },
    LoadGQL : ( query, callback, error ) =>
    {
        fetch( `https://api.maas.museum/graphql?query=${encodeURIComponent(query)}`).then((res) => res.json()).then((blob) => { callback(blob.data); }).catch (error);
    },
    GetQueryParams : () =>
    {
        try
        {
            var search = location.search.substring(1);
            var params = JSON.parse('{"' + search.replace(/&/g, '","').replace(/=/g,'":"') + '"}', function(key, value) { return key===""?value:decodeURIComponent(value) });
            return params;
        } catch ( e )
        {
            return {};
        }
    },
    CleanTitle : (title) =>
    {
        if ( title.indexOf ( " - " ) > -1 ) title = title.split ( " - ")[1];
        return title;
    }
};

export default axutils;
